<template>
	<div id="data_list" class="tac">
		<ul class="data_list_title clearfix">
			<li class="fl" v-for="list in datatitle">{{list.name}}</li>
		</ul>
		<ul class="data_list_con">
			<li v-for="list in datalists" class="clearfix">
				<span class="fl">{{list.time}}</span>
				<span class="fl">{{list.num}}</span>
				<span class="fl">{{list.name}}</span>
				<span class="fl">{{list.playlong}}</span>
			</li>
		</ul>
		<div class="page_con tac">
            <div class="in_page_con clearfix">   
                <div class="fl"><span> {{(page-1)*prePage+1}}-{{(page-1)*prePage+prePage}}</span><span>共{{count}}条</span></div>
                <ul class="clearfix fl">
                    <li class="fl" href="javascript:" @click="prevPage">上一页</li>
                    <li class="fl" href="javascript:;" @click="nextPage">下一页</li>
                    <li class="fl" href="javascript:;" @click="lastPage">尾页>></li>
                </ul>
            </div>
        </div>
	</div>	
</template>
<script>
	export default{
		name:'data_list',
		methods: {
			nextPage(){
                if((this.page)*this.prePage>this.count){
                    return false;
                }
                this.page++;
                this.getDataList(that.case);
            },
            prevPage(){
                if(this.page==1){
                    return false;
                }
                this.page--;
                this.getDataList(that.case);
            },
            lastPage(){
                this.page=parseInt(this.count/this.prePage)+1;
                this.getDataList(that.case);
            },
			getDataList(action){
                let that=this;                
                let params={
                    start:(that.page-1)*that.prePage,
                    count:that.prePage,
                    case:action
                };
                that.$http.post("//tob.vronline.com/getDayAppDate", params, {
                    emulateJSON: true,
                    credentials: true
                }).then(response => {
                    if (response.status != 200) {
                        return false;
                    }

                    if(!response.data.data){
                        that.datalists={};
                    }
                    that.datalists = response.data.data;
                    that.count=that.datalists.length;
                    that.$router.app.$emit('changeTitleList', response.data.sum)
                }, response => {
                    // error callback
                });
            },
		},
		created: function() {
			let that = this;
			that.getDataList(that.case);
			that.$router.app.$on('changeAction', function(i){
				that.page = 1;
				that.case = i;
                that.getDataList(i);
            });
		},
		data(){
			return{
				page:1,
                tp:1,
                count:0,
                prePage:5,
                case:'day',
				datatitle:[{
					name:"日期"
				},
				{
					name:"启动次数"
				},
				{
					name:"游戏名称"
				},
				{
					name:"游戏时长"
				}
				],
				datalists:[]
			}
		}
	}
</script>
<style>
	#data_list{padding: 0 20px;}
	.data_list_title li{width: 25%;line-height: 30px;background: rgba(47,53,68,.3);}
	.data_list_con span{width: 25%;display: inline-block;text-indent: -16px;line-height: 30px;}
    .in_page_con{margin: 20px 0 ;display: inline-block;}
    .in_page_con span{margin:0 6px;}
    .page_con ul{display: inline-block;margin-left: 20px;}
    .page_con ul li{cursor: pointer; }
</style>


